
.background {
    display: flex;
    height: 150px;
    align-items: center;
    align-content: center;
    justify-content: space-between;
    background-image: url(../../../public/imagens/fundo.png);
    background-color: #6278F7;
    text-align: center;
    padding: 0 128px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
}   

.background img{
    max-width: 60%;
}

ul {
    display: flex;
    flex-direction: row;
}

li {
    margin: 16px;
    list-style: none;
}

p {
    color: #fff;
}
